/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import Proptypes from 'prop-types';
import {
  Button,
  FormGroup,
  Input,
  Label,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import Select from 'react-select';

import IntlMessages from '../../helpers/IntlMessages';
import CustomSelectInput from '../common/CustomSelectInput';
import { hackthonTypeOptions } from '../../constants/defaultValues';
import instance from '../../helpers/axiosInstance';
import StateButton from '../StateButton';

const CreateHackathonModal = ({ setOpenCreateHackathon, id }) => {
  const [formValues, setFormValues] = useState({
    name: '',
    organizer_id: id,
    hackathon_type_id: 1,
  });

  const [selectedOption, setSelectedOption] = useState('');

  const handleChange = (event, key, value = false) => {
    setFormValues({
      ...formValues,
      [key]: value ? event : event.target.value,
    });
  };

  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const updateProfile = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/createHackathon',
            data: newData,
          });

          const { data } = response;
          if (data.success) {
            setFormValues({ ...formValues, ...newData });
            setOpenCreateHackathon(false);
            success('Hackathon created successfully!');
          } else {
            failed('Failed to create hackathon!');
          }
        } catch (error) {
          failed('Failed to create hackathon!');
        }
      };
      updateProfile();
    });
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.modal-title" />
        </h2>
        <p className="m-0">
          <IntlMessages id="challenges.modal.modal-sub-title" />
        </p>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.name" />
          </Label>
          <Input
            type="text"
            className="form-control"
            name="link-to-code-of-control"
            placeholder="Enter a Name"
            value={formValues.name}
            onChange={(event) => {
              handleChange(event, 'name');
            }}
          />
        </FormGroup>

        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.hackathon-type" />
          </Label>
          <Select
            components={{ Input: CustomSelectInput }}
            className="react-select"
            classNamePrefix="react-select"
            name="hackathon-type-id"
            value={selectedOption}
            onChange={(data) => {
              setSelectedOption(data);
              handleChange(data.value, 'hackathon_type_id', true);
            }}
            options={hackthonTypeOptions}
            placeholder="Enter a Hackthon Type"
          />
        </FormGroup>
      </ModalBody>

      <ModalFooter className="justify-content-end">
        <StateButton
          id="create-hackthon"
          color="primary"
          onClick={() => handleSubmit(formValues)}
          next={() => {
            window.location.reload(0);
          }}
        >
          Create Hackthon
        </StateButton>
        <Button color="danger" onClick={() => setOpenCreateHackathon(false)}>
          <IntlMessages id="challenges.modal.cancel-button" />
        </Button>
      </ModalFooter>
    </>
  );
};

CreateHackathonModal.proptype = {
  setOpenCreateHackathon: Proptypes.func.isRequired,
};

export default CreateHackathonModal;
