/* eslint-disable camelcase */
import React from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardText,
  CardTitle,
  Row,
} from 'reactstrap';
import { Separator } from '../../components/common/CustomBootstrap';
import { adminRoot } from '../../constants/defaultValues';
import IntlMessages from '../../helpers/IntlMessages';

const getStatusColor = (status) => {
  if (status === 0) return 'outline-success';
  if (status === 'ON GOING') return 'outline-warning';
  if (status === 'COMPLETED') return 'outline-danger';
  return '';
};

const getStatusText = (status) => {
  if (status === 0) return 'On Going';
  if (status === '') return 'outline-warning';
  if (status === 'COMPLETED') return 'outline-danger';
  return '';
};

const getTypeText = (status) => {
  if (status === 0) return 'Online';
  if (status === 1) return 'Blender';
  if (status === 2) return 'Offiline';
  return '';
};

const HackCard = ({ data }) => {
  const {
    id,
    name,
    tagline,
    status,
    hackathon_start,

    application_start,
    application_end,
    hackathon_type_id,
  } = data;
  return (
    <Card>
      <CardBody>
        <CardTitle>
          <p className="h2 lead">{name}</p>
        </CardTitle>

        <CardSubtitle>{tagline}</CardSubtitle>

        <CardTitle>
          <IntlMessages id="hackcard.hackthon" />
        </CardTitle>
        <CardSubtitle>
          <IntlMessages id="hackcard.starts" />
        </CardSubtitle>
        <CardText>{hackathon_start}</CardText>

        <CardSubtitle>
          <IntlMessages id="hackcard.ends" />
        </CardSubtitle>
        <CardText>{}</CardText>

        <Separator className="mb-4" />

        <CardTitle>
          <IntlMessages id="hackcard.application" />
        </CardTitle>
        <CardSubtitle>
          <IntlMessages id="hackcard.starts" />
        </CardSubtitle>
        <CardText>{application_start}</CardText>
        <CardSubtitle>
          <IntlMessages id="hackcard.ends" />
        </CardSubtitle>
        <CardText>{application_end}</CardText>

        <Badge
          color={getStatusColor(status)}
          pill
          outline
          className="mb-3 mr-3"
        >
          {getStatusText(status)}
        </Badge>

        <Badge color="outline-primary" pill outline className="mb-3">
          {getTypeText(hackathon_type_id)}
        </Badge>

        <Row className="justify-content-center">
          <Button className="m-1" href={`${adminRoot}/challenges/basics/${id}`}>
            <IntlMessages id="hackcard.edit-button" />
          </Button>
          <Button
            className="m-1"
            href={`${adminRoot}/challenges/details/${id}`}
          >
            <IntlMessages id="hackcard.view-details-button" />
          </Button>
        </Row>
      </CardBody>
    </Card>
  );
};

export default HackCard;
